<template>
    <div class="traffic">
        <!-- 头部 -->
        <div class="header row">
            <div class="cell cell-index">#</div>
            <div class="cell cell-status">Status</div>
            <div class="cell cell-method">Method</div>
            <div class="cell cell-protocol">Protocol</div>
            <div class="cell cell-host">Host</div>
            <div class="cell cell-path">Path</div>
            <div class="cell cell-type">Type</div>
            <div class="cell cell-time">Time</div>
        </div>
        <!-- 列表区域 -->
        <div class="el-table__body-wrapper">
            <!-- 优化后的列表展示组件，只展示显示区域 -->
            <list :total="$dc.total+20"
                  :height="height - 22"
                  :rowHeight="24">
                <template scope="props">
                    <div v-for="id in props.ids"
                         @click="clickRow(id)"
                         :class="{'selected': $dc.selectId == id}"
                         @contextmenu.prevent="$refs.ctx.open($event, id)"
                         class="record row">
                        <div class="cell cell-index">{{id+1}}</div>
                        <div class="cell cell-status">Status</div>
                        <div class="cell cell-method">Method</div>
                        <div class="cell cell-protocol">Protocol</div>
                        <div class="cell cell-host">Host</div>
                        <div class="cell cell-path">Path</div>
                        <div class="cell cell-type">Type</div>
                        <div class="cell cell-time">Time</div>
                    </div>
                </template>
            </list>
        </div>
        <context-menu id="testingctx" ref="ctx"
                      :ctxOpen="onCtxOpen"
                      :ctxCancel="resetCtxLocals"
                      :ctxClose="onCtxClose">
            <li class="ctx-item" @click="saveData">保存为mock数据</li>
            <li class="ctx-item" @click="copyUrl">复制url</li>
        </context-menu>
    </div>
</template>
<script>
    import _ from 'lodash';
    import List from './List.vue';
    import copyToClipboard from 'copy-to-clipboard';
    import ContextMenu from '../../context-menu';
    import dataApi from '../../api/data';
    import './httptraffic.pcss';
    export default {
        props: ['height'],
        components: { List, ContextMenu },
        data(){
            return {};
        },
        methods: {
            // -------------------------------菜单操作
            saveData(){
                if (!this.$dc.rightClickRow.resTime) {
                    this.$message({
                        message: '服务器还没有响应',
                        type: 'warning'
                    });
                    return;
                }
                this.$prompt('请输入数据文件名', '保存为数据文件', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消'
                }).then(({ value }) => {
                    dataApi.saveDataEntryFromTraffic(this.$dc.rightClickRow.idx, value
                        , this.$dc.rightClickRow.contentType.split(';')[0]).then((res) => {
                        var serverData = res.data;
                        if (serverData.code == 0) {
                            this.$message({
                                type: 'success',
                                message: '保存成功!'
                            });
                        } else {
                            this.$message.error(`出错了，${serverData.msg}`);
                        }
                    });
                });
            },
            // 复制url
            copyUrl(){
                copyToClipboard(`${this.$dc.rightClickRow.protocol}://${this.$dc.rightClickRow.host}${this.$dc.rightClickRow.path}`);
                this.$message('已将url复制到剪切板');
            },
            // 点击行
            clickRow(id){
                this.$dc.setCurrentRowIndex(id);
            },
            // -------------------------------右击菜单显示
            // 打开菜单
            onCtxOpen(id) {
                this.$dc.rightClickRow = this.$dc.rows[id];
            },
            // 点击菜单选项
            onCtxClose(locals) {
            },
            // 点击空白地方
            resetCtxLocals() {
                this.$dc.rightClickRow = {};
            }
        }
    };
</script>


